.Alumno {
  margin: 10px 40px 10px 40px;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: #3d0041;
  border-radius: 5px;
  transition: all 6s linear;
}
.Imagen {
  display: none;
}
.Datos {
  display: flex;
  justify-content: space-around;
  width: 100%;
}
.Alumno > .Imagen {
  width: 75px;
  height: 75px;
  background-size: cover;
  text-align: center;
  align-items: center;

  border-radius: 20%;
}
.Alumno.activo {
  cursor: pointer;
  height: max-content;
  transition: all 2s ease-in-out;
}

.Alumno.activo > .Imagen {
  display: flex;
}

.Alumno.activo > .Datos {
  width: 50%;
  flex-direction: column;
  align-items: flex-start;
}
.Alumno > .cargando {
  background-color: #3d0041;
  overflow: hidden;
}
.Alumno > .cargando::after {
  content: "";
  width: 100%;
  height: 100%;
  position: relative;
  transform: translateY(-100%);
  animation: cargando 1s infinite;
}
.Alumno > .cargando::before {
  content: "";
  width: 100%;
  height: 100%;
  position: relative;
  transform: translateY(-100%);
  animation: cargando 1s infinite;
}

.Alumno > .cargado {
  background-image: url("../public/alumno.png");
}

@keyframes cargando {
  0% {
    background-color: #3d0041;
  }
  100% {
    transform: translateY(100%);
    background-color: #330036;
  }
}
